import { precompileTemplate } from "@ember/template-compilation";
export default precompileTemplate("<div class={{this.styles.container}}>\n  <h1 class={{this.styles.title}} data-test-page-title>\n    {{@title}}\n  </h1>\n\n  <div\n    class={{this.styles.content}}\n    data-test-page-content\n    id=\"main-content\"\n    tabindex=\"-1\"\n  >\n    {{yield\n      (hash\n        Demo=(component \"ui/page/demo\")\n        Section=(component \"ui/page/section\")\n        Subsection=(component \"ui/page/subsection\")\n      )\n    }}\n  </div>\n</div>")